/* Global styles */
body {
  font-family: Arial, Helvetica, sans-serif;
  color: #666666;
}

a, a:visited {
  color: #666666;
}

.clear {
  clear: both;
}

td {
  border-top: solid 1px #d3d3d3;
}

td, th {
  padding: 10px;
}

table {
  border-collapse:collapse;
}

/* Documentation site component styles */
.example {
  border: solid 1px #d3d3d3;
  padding: 16px;
  margin-bottom: 16px;
  background-color: #f4f6f9;
}

.example h4:first-of-type {
   margin-top: 0;
}

.example p:last-of-type {
   margin-bottom: 0;
}

.props tr td:last-of-type {
  text-align: center;
}

.props th, td {
  text-align: left;
  padding: 5px;
}

.navigation {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 33px;
  font-size: 26px; 
  width: 250px;
  float: left;
  list-style-type: none !important;
  padding: 0;
}

.navigation-item {
  margin: 10px;
}

.navigation-item > a {
  text-decoration: none !important;  
}

.componentpage {
  margin-left: 275px;
  margin-top: 26px;
}

.show-code-button {
  margin-top: 26px;
}


